import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import api from '@/api/api.js'
import orderList from '../../json.js'
export default {
	data() {
		return {
			navItems:[{
					state: 0,
					text: '待付款',
					loadingType: 'loaded',
					orderList: []
				},
				{
					state: 1,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '售后',
					loadingType: 'more',
					orderList: []
				}
			],
			// curNav:0,
			list:[0,1,2,3,4],
			tabCurIndex:0,
			orderList:orderList.orderList,
			isGetData:true
		};
	},
	onLoad(options) {
		this.tabCurIndex = +options.state;
		this.loadAllData()
	},
	components:{
		uniNavBar
	},
	methods:{
		tabClick(index){
			this.tabCurIndex = index;
			// this.loadData()
		},
		changeTab(e){
			this.tabCurIndex = e.target.current
			this.loadData('tabChange')
		},
		loadData(source){
			// this.tabCurIndex = Number(source);
			// let state = this.navItems[index].state
			// let isLoad = this.isGetData;
			let index = this.tabCurIndex;
			let listData = this.navItems;
			if(source === 'tabChange' ) return
			let _list = listData[index].orderList
			_list.forEach((item)=>{
				_list.push(item)
			})
			// this.$forceUpdate()
		},
		loadAllData(){
			let listData = this.navItems;
			this.orderList.forEach((item)=>{
				this.navItems.forEach((nitem,id) =>{
					if(item.state === nitem.state){
					  listData[id].orderList.push(item)
					}
				})
			})
		}
	}
}
